import React from "react"
import { Container, Row, Col } from "react-bootstrap"
import socialMediaData from "../information/socialmedia"
import MediaButton from "./MediaButton"

class Header extends React.Component {
  render() {
    return (
      <header className="masthead">
        <Container className="h-100">
          <Row className="h-100 align-items-center">
            <Col md={4} className="text-center">
              <img className="img img-fluid" src="/images/me.jpg" alt="Me" />
            </Col>
            <Col md={8} className="text-center">
              <h1>Hi, I'm Sameeha.</h1>
              <h5>
                Software Engineer | Web and Mobile Dev | Woman in Engineering
              </h5>
              <a
                href="./Rahman,Sameeha.pdf"
                className="btn btn-primary btn-masthead"
                download
              >
                Download CV
              </a>{" "}
              <br />
              {socialMediaData.map((media, index) => (
                <MediaButton key={index} item={media} />
              ))}
            </Col>
          </Row>
        </Container>
      </header>
    )
  }
}

export default Header
