import React from "react"
import Header from "../components/Header"
import { Container, Col, Row } from "react-bootstrap"
import workData from "../information/work"
import WorkItem from "../components/WorkItem"
import educationData from "../information/education"
import { Link } from "gatsby"
import volunteeData from "../information/volunteer"
import competitionData from "../information/competition"
import TopNavBar from "../navigation/NavBar"
import Footer from "../navigation/Footer"
import Layout from "../components/Layout"

class Home extends React.Component {
  render() {
    return (
      <Layout>
        <TopNavBar />
        <Header />
        <Container fluid>
          <Row>
            <Col lg={{ span: 8, offset: 2 }}>
              <section className="py-5" id="about">
                <h2 className="section-heading">About Me</h2>
                <p className="section-descrip text-justify">
                  A degree-educated developer with two academic qualifications
                  in the field, with working knowledge in many web and hybrid
                  mobile technologies, such as React.js, Angular, React Native
                  and Flutter. An engineer who stays up to date on recent
                  technologies and inventions in the fields of science and
                  technology, who constantly revises software best practices and
                  user experience guidelines. A blogger at two popular online
                  news platforms, with articles in multiple technical topics. An
                  award-winning event organizer of hackathons, with experience
                  in chairing tech conferences.
                </p>
              </section>
              <section className="py-5" id="work">
                <h2 className="section-heading">Experience</h2>
                {workData.map((item, index) => (
                  <WorkItem key={index} item={item} />
                ))}
              </section>
              <section className="py-5" id="education">
                <h2 className="section-heading">Education</h2>
                {educationData.map((item, index) => (
                  <WorkItem key={index} item={item} />
                ))}
              </section>
              <section className="py-5" id="projects">
                <h2 className="section-heading">Projects</h2>
                <p className="section-descrip">
                  I have experience in multiple technologies, though my fields
                  of interest is HCI, UX, Web and Hybrid Mobile technologies.
                  <br /> For a list of my projects in those fields, please click{" "}
                  <Link to="/projects">here</Link>.
                </p>
              </section>
              <section className="py-5" id="volunteer">
                <h2 className="section-heading">Volunteering</h2>
                {volunteeData.map((item, index) => (
                  <WorkItem key={index} item={item} />
                ))}
              </section>
              <section className="py-5" id="competition">
                <h2 className="section-heading">Competitions</h2>
                {competitionData.map((item, index) => (
                  <WorkItem key={index} item={item} />
                ))}
              </section>
              <section className="py-5" id="posts">
                <h2 className="section-heading">Blog Posts</h2>
                <p className="section-descrip">
                  I have published articles in popular platforms on the topic of
                  mobile technology.
                  <br /> For a full list of my blog posts, please click{" "}
                  <Link to="/posts">here</Link>.
                </p>
              </section>
            </Col>
          </Row>
        </Container>
        <Footer />
      </Layout>
    )
  }
}

export default Home
